.button {
    background-color: transparent;
    border-color: #1a8ed1;
    color: #1a8ed1;
}

.button:hover,
.button:active {
    color: white;
}

.button span {
    margin: 0 0.5rem;
}

.badge {
    background-color: #1a8ed1;
    border-radius: 30px;
    padding: 0.15rem 1.25rem;
    color: #1d1d1d;
}