.loading_spinner__1djdG {
    height: 50px;
    width: 50px;
    border-left: 3px solid #1a8ed1;
    border-bottom: 3px solid #1a8ed1;
    border-right: 3px solid #1a8ed1;
    border-top: 3px solid transparent;
    border-radius: 50%;
    -webkit-animation: loading_spinner__1djdG 1s linear infinite;
            animation: loading_spinner__1djdG 1s linear infinite;
}
 
@-webkit-keyframes loading_spinner__1djdG {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
 
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
 
@keyframes loading_spinner__1djdG {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
 
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
